<template>
  <div class="tax-page tempate">
    <h4>模板管理</h4>
    <div class="content">
      <div class="options">
        <span>配置需要使用的模板,列举了所有可能会用到的报表</span>
        <div class="btns">
          <el-button type="primary" @click="downloadXml" :loading="multipartFile.loading">导出Xml</el-button>
          <el-button type="success" @click="downloadExcel" :loading="multipartFile.loading">导出Excel</el-button>
        </div>
      </div>
      <table class="table sample-table table-bordered table-hover">
        <thead>
        <tr>
          <th scope="col">模板名称</th>
          <th scope="col" width="80">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in templates" :key="item.id" :class="(item.configAble != true)?'tr-none':''"
            v-if="(item.sort !=999 || shows)">
          <td><span class="-child" v-if="!!item.id && !!item.multiAble"></span>
            <span>{{item.templateName}}</span>
            <span class="td-tip" v-if="!(!!item.id && !!item.multiAble)">{{item.templateTypeDescs}}</span>
          </td>
          <td class="td-ops">
            <swak-link v-if="item.id" :to="'/func/template/cfg/' + item.id">配置</swak-link>
            <swak-link v-else :to="'/func/template/add?type=' + item.templeteType">配置</swak-link>
          </td>
        </tr>
        </tbody>
      </table>
      <swak-link v-if="!shows" class="show-more" @link:click="shows = !shows">显示更多...</swak-link>
    </div>
    
    <multi-file :visible.sync="multipartFile.visible" :file="multipartFile"></multi-file>
  </div>
</template>

<script>
  import SwakLink from "../../ui/components/link/link";
  import TemplateApi from "@/api/manage/template";
  import ElButton from "element-ui/packages/button/src/button";
  import MultiFile from "../../ui/components/download/multiFile";

  export default {
    components: {
      MultiFile,
      ElButton,
      SwakLink
    },
    name: "manager",
    data() {
      return {
        templates: [],
        shows: false,
        multipartFile: {
          loading: false,
          visible: false,
        }
      }
    },
    mounted() {
      this.$emit('layout:load', "template")
      this.loadTemplates();
    },
    methods: {
      loadTemplates: function () {
        TemplateApi.list().then(res => {
          this.templates = res.obj;
        })
      },
      downloadXml: function () {
        this.multipartFile.loading = true;
        TemplateApi.xml().then(res => {
          this.multipartFile.loading = false
          this.multipartFile.name = decodeURI(res.fileName)
          this.multipartFile.data = res.data
          this.doDownload();
        }, () => {
          this.$notify.error("error!")
        })
      },
      downloadExcel: function () {
        this.multipartFile.loading = true;
        TemplateApi.excel().then(res => {
          this.multipartFile.loading = false
          this.multipartFile.name = decodeURI(res.fileName)
          this.multipartFile.data = res.data
          this.doDownload();
        }, () => {
          this.$notify.error("error!")
        })
      },
      doDownload: function () {
        let url = window.URL.createObjectURL(new Blob([this.multipartFile.data]))
        let link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', this.multipartFile.name)
        document.body.appendChild(link)
        link.click()
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @require "../../ui/common/stylus/variable.styl"
  .tempate {
    .td-tip {
      color $color-light-grey;
      margin-left 10px;
    }
    .-child {
      display inline-block;
      width 25px;
      height 15px;
      border-left 1px solid $color-info;
      border-bottom 1px solid $color-info;
      margin-top 0;
      margin-bottom 0;
      margin-left 10px;
      margin-right 20px;
    }
    .tr-none {
      color $color-light-grey;
    }
    .show-more {
      padding 10px 0;
      display block;
      color $color-primary;
      font-size 14px;
      cursor pointer;
    }
  }
</style>