            class="-ops">点击下载</span></a>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "multi-download",
    props: {
      visible: {
        type: Boolean,
        default: false
      },
      file: {
        type: Object,
        default: null
      }
    },
    methods: {
      doCancel: function () {
        this.$emit('update:visible', false)
      },
    }
  }
</script>

