import fetch from '../fetch'

/**
 * Subscribe service
 */
const Templates = {
  list: () => fetch({
    url: '/api/user/template/list',
    method: 'get'
  }),
  tools: () => fetch({
    url: '/api/user/template/tools',
    method: 'get'
  }),
  def: (type) => fetch({
    url: '/api/user/template/def?type=' + type,
    method: 'get'
  }),
  get: (id) => fetch({
    url: '/api/user/template/get/' + id,
    method: 'get'
  }),
  save: (data) => fetch({
    url: '/api/user/template/save',
    method: 'post',
    data: data
  }),
  forceSave: (data) => fetch({
    url: '/api/user/template/forceSave',
    method: 'post',
    data: data
  }),
  xml: () => fetch({
    url: '/api/user/template/xml',
    method: 'post',
    responseType: 'blob',
    data: {}
  }),
  excel: () => fetch({
    url: '/api/user/template/excel',
    method: 'post',
    responseType: 'blob',
  }),
  excelOne: (id) => fetch({
    url: '/api/user/template/excel/' + id,
    method: 'post',
    responseType: 'blob',
  }),
};

export default Templates